/**
 * CSS
 ============================================================================ */
import "./Leads.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import ErrorBoundary from "../errorBoundary";

/**
    * User imports
    ============================================================================ */
import Alert from "../alert";
import LanguageSelector from "../language-selector";

/**
 * Default export
 */
export default (args) => {
  /**
      * Default Properties
      ============================================================================ */
  let defaults = { namespace: "Leads" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
      * Barrels
      ============================================================================ */
  const { signedIn, language } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getContainerClass } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
      * Destructured vars
      ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const { wrapperClass, innerClass } = getContainerClass(signedIn);

  const appClass = signedIn ? "App" : "App bg";
  // const appClass = "App";

  /**
      * Functions
      ============================================================================ */
  const getJSX = () => {
    let leadFile = null;
    switch (window.location.pathname) {
      case "/":
      case "/sign-in":
        leadFile = "LeadLogIn.js";
        break;

      case "/sign-up":
        leadFile = "LeadSignUp.js";
        break;

      default:
        leadFile = "LeadLogIn.js";
        break;
    }

    const ExternalFile =
      leadFile !== null
        ? React.lazy(() => import(`../locale/${language}/leads/${leadFile}`))
        : "";

    return (
      <div className={appClass} style={{ padding: "0 0% 0 7%" }}>
        {/**
         * The alert
         */}
        <Alert data={args} />

        {/**
         * Wrapper for the main window
         */}
        <div className={wrapperClass} style={{}}>
          {/**
           * Top margin of the main window (only for >= sm)
           */}
          <div className="d-none d-md-block" style={{ height: "1rem" }}></div>

          {/**
           * The main window
           */}
          <div
            className=""
            style={{
              background: "Transparent",
              boxShadow: "none",
              paddingRight: "4rem",
            }}
          >
            <ErrorBoundary>
              <ExternalFile {...args} />
            </ErrorBoundary>

            {/**
             * Footer
             */}
            <div className="text-center">
              <span style={{}}>
                <LanguageSelector {...args} />
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  };

  /**
      * Return
      ============================================================================ */
  return <>{getJSX()}</>;
};
