import React, { useEffect } from "react";
import { useTranslation, Trans } from "react-i18next";
import {
  Form,
  InputGroup,
  // Button,
  Container,
  Row,
  Col,
  // Spinner,
} from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import SubmitButton from "../submitButton";
// import Alert from "../alert";
// import LanguageSelector from "../language-selector";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  // console.log("ALERT DATA", props);

  /**
   * Barrels
   */
  const { ip, logInEmail, logInPassword } = props.barrelGetters;
  const {
    setLoggingIn,
    setUpdateInterval,
    setLogInEmail,
    setLogInPassword,
  } = props.barrelSetters;
  const {
    getReviser,
    getWorkplace,
    sendRequest,
    getUri,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  const { t } = useTranslation("LogIn");

  /**
   * Create refs
   */
  const refEmail = React.createRef();
  const refPassword = React.createRef();

  useEffect(() => {
    const tempRefEmail = refEmail.current;
    const tempRefPassword = refPassword.current;
    return () => {
      if (tempRefEmail !== null && logInEmail !== tempRefEmail.value)
        setLogInEmail(tempRefEmail.value);

      if (tempRefPassword !== null && logInPassword !== tempRefPassword.value)
        setLogInPassword(tempRefPassword.value);
    };

    // eslint-disable-next-line
  }, []);

  /**
   * Yup schema
   */
  const schema = yup.object({
    email: yup
      .string()
      .email(t("Invalid email address"))
      .required(t("Email is required")),
    password: yup.string().required(t("No password provided")),
  });

  /**
   * Send user registration request
   * @param {object} data
   */
  const sendUserLogInRequest = (data) => {
    // data.pw = data.password;
    data.reviser = getReviser();
    data.workplace = getWorkplace();
    data.ip = ip;
    data.uri = getUri();
    sendRequest({ req: "sign in user", data });
  };

  /**
   * Event listener: On Form Submit
   * @param {object} data Form data
   */
  const onSubmit = (data) => {
    setUpdateInterval(-1);
    setLoggingIn(true);
    sendUserLogInRequest(data);
  };

  const UserLogInForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      <h3>{t("Sign In")}</h3>

      {/**
       * Email
       */}
      <Form.Group controlId="validationFormikEmail">
        <Form.Label>{t("Email address")}</Form.Label>
        <InputGroup>
          <Form.Control
            placeholder={t("Enter email")}
            name="email"
            value={values.email}
            ref={refEmail}
            // autoFocus
            onChange={handleChange}
            isInvalid={!!errors.email && touched.email}
          />
          <Form.Control.Feedback type="invalid">
            {errors.email}
          </Form.Control.Feedback>
        </InputGroup>
      </Form.Group>

      {/**
       * Password
       */}
      <Form.Group controlId="validationFormikPassword">
        <Form.Label>{t("Password")}</Form.Label>
        <InputGroup>
          <Form.Control
            type="password"
            placeholder={t("Enter password")}
            name="password"
            value={values.password}
            ref={refPassword}
            onChange={handleChange}
            isInvalid={!!errors.password && touched.password}
          />
          <Form.Control.Feedback type="invalid">
            {errors.password}
          </Form.Control.Feedback>
        </InputGroup>
      </Form.Group>

      {/**
       * Submit button
       */}
      <Form.Group controlId="validationFormikPassword">
        <SubmitButton
          {...args}
          caption={t("Submit")}
          request="sign in user"
          namespace="LogIn"
        />
      </Form.Group>

      <Container fluid="md">
        <Row>
          {/**
           * Register
           */}
          <Col>
            <p className="other-options text-left">
              <Trans i18nKey="LogIn:Not yet registered">
                Not yet <a href="/sign-up">registered?</a>
              </Trans>
            </p>
          </Col>

          {/**
           * Forgot password
           */}
          <Col>
            <p className="other-options text-right">
              <Trans i18nKey="LogIn:Forgot password">
                Forgot <a href="/reset">password?</a>
              </Trans>
            </p>
          </Col>
        </Row>
      </Container>
    </Form>
  );

  return (
    <>
      <Formik
        component={UserLogInForm}
        validationSchema={schema}
        onSubmit={onSubmit}
        initialValues={{
          email: logInEmail,
          password: logInPassword,
        }}
      />
    </>
  );
};
