// System CSS
// import "react-datepicker/dist/react-datepicker.css";

// System imports
import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Form, Alert, Col } from "react-bootstrap";
// import { Redirect } from "react-router-dom";
// import ChevronRight from "@material-ui/icons/ArrowForwardIos";
import { KeyboardArrowRight } from "@material-ui/icons";
// import moment from "moment-timezone";
// import DatePicker from "react-datepicker";
// import { Today } from "@material-ui/icons";
// import { Today } from "@material-ui/icons";
// import DatePicker from "react-date-picker";
// import MomentUtils from "@date-io/moment";
// import { MuiPickersUtilsProvider } from "@material-ui/pickers";
// import { DatePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
// import _ from "lodash";

// User imports
import Card from "../card";
import { Formik } from "formik";
import * as yup from "yup";
import { useStickyState } from "../../utils/state";
// import Dropzone from "../dropzone";
import SubmitButton from "../submitButton";
import ModalMessage from "../modalMessage";
// import { useStickyState } from "../../utils/state";
import timezones from "./timezones.en.json";

// const UserAgreement = React.lazy(() => import("../locale/en/UserAgreement"));

export default (args) => {
  // console.log("ACCOUNT REGISTRATION ARGS:", args);
  /**
   * Default Properties
   */
  let defaults = {
    request: "get user sequence", // "register account",
    namespace: "AccountRegistration",
  };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const {
    signedIn,
    accountNumber,
    // accountPhotoPath,
    userData,
    language,
    userBirthYear,
    userBirthMonth,
    userBirthDay,
    userBirthDateError,
    userTimezone,
    debug,
  } = props.barrelGetters;
  const {
    setUserBirthYear,
    setUserBirthMonth,
    setUserBirthDay,
    setUserBirthDateError,
    setUserTimezone,
  } = props.barrelSetters;
  const {
    getReviser,
    getWorkplace,
    sendRequest,
    // formatDate,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Hooks
   */
  // const [redirect, setRedirect] = useState(false);
  const [userAgreementShow, setUserAgreementShow] = useStickyState(
    false,
    "userAgreementShow"
  );
  const [privacyPolicyShow, setPrivacyPolicyShow] = useStickyState(
    false,
    "privacyPolicyShow"
  );

  // const [offsetList, setOffsetList] = useStickyState(null, "offsetList");
  // const [calenarVisible, setCalenarVisible] = useState(false);
  // const [birthDateError, setBirthDateError] = useState("");
  // const [birthDate, setBirthDate] = useState("");

  // const [birthYear, setBirthYear] = useState("");
  // const [birthMonth, setBirthMonth] = useState("");
  // const [birthDay, setBirthDay] = useState("");

  /**
   * Custom hooks
   */
  const { t } = useTranslation(props.namespace);

  /**
   * Yup schema
   */
  const schema = yup.object({
    // dropzoneHook: yup.string().required(t("Photo ID is required")),
    // dropzoneHook: yup.string().optional(),
    // terms: yup.array().required(t("Terms is required")),
    timezone: yup.string().required(t("Timezone at birth is required")),
    birthYear: yup.number().required(t("Year is required")),
    birthMonth: yup.number().required(t("Month is required")),
    birthDay: yup.number().required(t("Day is required")),
    // birthDateHook: yup.string().required(t("Birth date is required")).length(5),
    // birthDate: yup
    //   .string()
    //   .required(t("Date of birth is required"))
    //   // .max(new Date(), t("Date of birth must be today or earlier"))
    //   // .string()
    //   .matches(new RegExp(t("Date Regex")), t("Wrong date format")),

    // .test({
    //   name: "regex",
    //   exclusive: true,
    //   params: {},
    //   message: t("Date must be valid"),
    //   test: (value) => {
    //     if (typeof value === "undefined") return false;

    //     // return Date.parse(value);
    //     return value !== "error";

    //     console.log("TEST");

    //     // Test regex
    //     // if (!RegExp(t("Date Regex")).test(value)) return false;

    //     let date2en;
    //     try {
    //       date2en = formatDate(
    //         value,
    //         { month: "numeric", day: "numeric", year: "numeric" },
    //         false,
    //         "en-US"
    //       );
    //     } catch (error) {
    //       console.log("error", error);
    //       return false;
    //     }

    //     // // console.log("date2en", date2en);
    //     // console.log("date2en", date2en);

    //     // // console.log("IS DATE", _.isDate(new Date(date2en)));
    //     // console.log("IS DATE", _.isDate(new Date(date2en)));

    //     return true;
    //   },
    // }),
  });

  /**
   * Functions
   */

  /**
   * Send account registration request
   * @param {object} data
   */
  const sendAccountRegistrationRequest = (data) => {
    if (data === null) return false;

    data.reviser = getReviser();
    data.workplace = getWorkplace();
    data.email = userData.email;

    return sendRequest({ req: props.request, data });
  };

  /**
   * Event listener: On Form Submit
   * @param {object} data Form data
   */
  const onSubmit = (data) => {
    delete data.birthYear;
    delete data.birthMonth;
    delete data.birthDay;

    const newDate = composeDate(userBirthYear, userBirthMonth, userBirthDay);
    data.birthDate = newDate;

    if (newDate !== "") {
      // console.log(data);
      if (sendAccountRegistrationRequest(data)) {
        // Account registration request was submitted
      }
    }
  };

  // const timezones = 1;
  // const timezones_sorted = timezones.sort((a, b) =>
  //   (a.Offset + 12) % 24 > (b.Offset + 12) % 24
  //     ? 1
  //     : (a.Offset + 12) % 24 === (b.Offset + 12) % 24
  //     ? a.Name > b.Name
  //       ? 1
  //       : -1
  //     : -1
  // );

  const timezones_sorted = timezones;

  const isValidDate = (dateString) => {
    let dateformat = /^(0?[1-9]|1[0-2])[\/](0?[1-9]|[1-2][0-9]|3[01])[\/]\d{4}$/;

    // Match the date format through regular expression
    if (dateString.match(dateformat)) {
      let operator = dateString.split("/");

      // Extract the string into month, date and year
      let datepart = [];
      if (operator.length > 1) {
        datepart = dateString.split("/");
      }
      let month = parseInt(datepart[0]);
      let day = parseInt(datepart[1]);
      let year = parseInt(datepart[2]);

      // Create list of days of a month
      let ListofDays = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
      if (month === 1 || month > 2) {
        if (day > ListofDays[month - 1]) {
          ///This check is for Confirming that the date is not out of its range
          return false;
        }
      } else if (month === 2) {
        let leapYear = false;
        if ((!(year % 4) && year % 100) || !(year % 400)) {
          leapYear = true;
        }
        if (leapYear === false && day >= 29) {
          return false;
        } else if (leapYear === true && day > 29) {
          // console.log("Invalid date format!");
          return false;
        }
      }
    } else {
      // console.log("Invalid date format!");
      return false;
    }
    return true;
  };

  const composeDate = (year, month, day) => {
    // console.log(year, month, day);
    const newDate = `${month}/${day}/${year}`;
    // const newDate = `${year}-${month}-${day}`;
    if (isValidDate(newDate)) {
      // setBirthDateComposed(newDate);
      if (year !== "" && month !== "" && day !== "") {
        // console.log("ok");
        setUserBirthDateError("");
        // setBirthDate(newDate);

        // return "ok";
        return newDate;
      } else {
        // console.log("incomplete");
        setUserBirthDateError("");
        // return "incomplete";
        return "";
      }
    } else {
      // setBirthDateComposed("error");
      if (year !== "" && month !== "" && day !== "") {
        // console.log("error");
        setUserBirthDateError(t("Date must be valid"));
        // return "error";
        return "";
      } else {
        // console.log("incomplete");
        setUserBirthDateError("");
        // return "incomplete";
        return "";
      }
    }
  };

  // let timeZoneID = 0;
  // let prevTimeZoneID = 0;
  const yearNow = new Date().getFullYear();

  const AccountRegistrationForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    setFieldValue,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      <Form.Row style={{ margin: "0 0" }}>
        <Form.Group
          as={Col}
          controlId="formGridDateOfBirth"
          style={{ margin: "0 0" }}
        >
          <Form.Label>{t("Date of birth")}</Form.Label>
        </Form.Group>
      </Form.Row>
      <Form.Row>
        <Form.Group as={Col} controlId="formGridYear">
          <Form.Control
            custom
            as="select"
            name="birthYear"
            value={values.birthYear}
            // autoFocus
            onChange={(e) => {
              handleChange(e);
              setUserBirthYear(e.target.value);
              setUserBirthDateError("");
            }}
            isInvalid={!!errors.birthYear && touched.birthYear}
          >
            <option key="" value="">
              {t("Year")}
            </option>
            {Array.from(Array(120).keys()).map((key) => {
              let year = yearNow - key;

              return (
                <option key={key} value={year}>
                  {year}
                </option>
              );
            })}
          </Form.Control>
          <Form.Control.Feedback type="invalid">
            {errors.birthYear}
          </Form.Control.Feedback>
          <div className="invalid-feedback-custom">{userBirthDateError}</div>
        </Form.Group>

        <Form.Group as={Col} controlId="formGridMonth">
          <Form.Control
            custom
            as="select"
            name="birthMonth"
            value={values.birthMonth}
            onChange={(e) => {
              handleChange(e);
              setUserBirthMonth(e.target.value);
              setUserBirthDateError("");
            }}
            isInvalid={!!errors.birthMonth && touched.birthMonth}
          >
            <option key="" value="">
              {t("Month")}
            </option>
            {Array.from(Array(12).keys()).map((key) => {
              let date = new Date(`${key + 1}/1/1970`);
              let monthName = date.toLocaleString(language, { month: "long" });
              let monthNumber = (key + 1).toString().padStart(2, "0");
              return (
                <option key={key} value={monthNumber}>
                  {monthNumber} - {monthName}
                </option>
              );
            })}
          </Form.Control>
          <Form.Control.Feedback type="invalid">
            {errors.birthMonth}
          </Form.Control.Feedback>
        </Form.Group>

        <Form.Group as={Col} controlId="formGridDay">
          <Form.Control
            custom
            as="select"
            name="birthDay"
            value={values.birthDay}
            onChange={(e) => {
              handleChange(e);
              setUserBirthDay(e.target.value);
              setUserBirthDateError("");
            }}
            isInvalid={!!errors.birthDay && touched.birthDay}
          >
            <option key="" value="">
              {t("Day")}
            </option>
            {Array.from(Array(31).keys()).map((key) => {
              let dayNumber = (key + 1).toString().padStart(2, "0");
              return (
                <option key={key} value={dayNumber}>
                  {dayNumber}
                </option>
              );
            })}
          </Form.Control>
          <Form.Control.Feedback type="invalid">
            {errors.birthDay}
          </Form.Control.Feedback>
        </Form.Group>
      </Form.Row>

      <Form.Group controlId="validationFormikTimezone">
        <Form.Label>{t("Timezone at birth")}</Form.Label>
        <Form.Control
          as="select"
          custom
          name="timezone"
          value={values.timezone}
          onChange={(e) => {
            handleChange(e);
            setUserTimezone(e.target.value);
          }}
          isInvalid={!!errors.timezone && touched.timezone}
        >
          <option value="">{t("Choose...")}</option>
          {timezones_sorted.map((element, key) => (
            <option
              key={key}
              value={`${(((element.Offset + 12) % 24) + 1)
                .toString()
                .padStart(2, "0")} - ${element.Name}`}
            >
              {element.Name}
            </option>
          ))}
        </Form.Control>
        <Form.Control.Feedback type="invalid">
          {errors.timezone}
        </Form.Control.Feedback>
      </Form.Group>

      <div style={{ paddingBottom: "10px" }}>
        {t("Agree to terms and conditions")}
      </div>

      <Trans i18nKey="AccountRegistration:The User Agreement and the Privacy Policy">
        <KeyboardArrowRight style={{ color: "var(--dark)" }} />
        <a href="#" onMouseDown={() => setUserAgreementShow(true)}>
          User Agreement
        </a>

        <br />

        <KeyboardArrowRight style={{ color: "var(--dark)" }} />
        <a href="#" onMouseDown={() => setPrivacyPolicyShow(true)}>
          Privacy Policy
        </a>
      </Trans>

      <br />
      <br />

      <SubmitButton
        {...args}
        caption={t("Submit")}
        request={props.request}
        namespace={props.namespace}
      />
    </Form>
  );

  if (signedIn === false)
    return (
      <>
        <Alert variant="info">
          <Alert.Heading>{t("User not logged in")}</Alert.Heading>
          <p>
            <Trans i18nKey="AccountRegistration:Please log in first">
              Please log in first.
            </Trans>
          </p>
        </Alert>
      </>
    );

  if (!debug && accountNumber !== null)
    return (
      <>
        <Alert variant="info">
          <Alert.Heading>{t("Account successfully registered")}</Alert.Heading>
          <p>
            <Trans
              i18nKey="AccountRegistration:Account with your personal ID successfully registered"
              accountNumber={accountNumber}
            >
              Account with the personal ID
              <strong>{{ accountNumber }}</strong>
              successfully registered.
            </Trans>
          </p>
        </Alert>
      </>
    );

  return (
    <>
      <ModalMessage
        show={userAgreementShow}
        file={`${language}/UserAgreement`}
        size="lg"
        centered={false}
        title={t("User Agreement")}
        footer=""
        onHide={() => setUserAgreementShow(false)}
      />

      <ModalMessage
        show={privacyPolicyShow}
        file={`${language}/PrivacyPolicy`}
        size="lg"
        centered={false}
        title={t("Privacy Policy")}
        footer=""
        onHide={() => setPrivacyPolicyShow(false)}
      />

      <Card title={t("Open account")}>
        <Formik
          component={AccountRegistrationForm}
          validationSchema={schema}
          onSubmit={onSubmit}
          initialValues={{
            // personalID: "",
            // terms: "",
            // dropzoneHook: accountPhotoPath,
            timezone: userTimezone,
            // birthDate: "",
            birthYear: userBirthYear,
            birthMonth: userBirthMonth,
            birthDay: userBirthDay,
            // birthDateHook: "",
          }}
        />
      </Card>
    </>
  );
};
