/**
 * CSS
 */
// import "bootstrap/dist/css/bootstrap.min.css";
import "../main/Main.css";

/**
 * System imports
 */
import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { useTranslation } from "react-i18next";

/**
 * User imports
 */
import Alert from "../../alert";
import LanguageSelector from "../../language-selector";
import NotConnected from "../../notConnected";
// import { useEventListener } from "../../../utils/useEventListener";

/**
 * Component: Main
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Main" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);
  const barrels = args;

  /**
      * Barrels
      ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getContainerClass, getRedirect } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  const signedIn = false;

  // const { t } = useTranslation(props.namespace);

  const { wrapperClass, innerClass } = getContainerClass(signedIn);

  const appClass = signedIn ? "App" : "App bg";

  // if (!signedIn) setRedirectTo("/sign-in");

  return (
    <>
      <Router>
        {getRedirect()}

        <div className={appClass} style={{}}>
          {/**
           * Wrapper for the main window
           */}
          <div className={wrapperClass} style={{}}>
            {/**
             * Top margin of the main window (only for >= sm)
             */}
            <div className="d-none d-md-block" style={{ height: "1rem" }}></div>

            {/**
             * The main window
             */}
            <div className={innerClass}>
              {/**
               * Top padding of the main window
               */}
              <div style={{ height: "16px" }}></div>

              {/**
               * The alert
               */}
              <div>
                <Alert data={barrels} />
              </div>

              {/**
               * Routes
               */}
              <Switch>
                {/**
                 * Route: Home
                 */}

                <Route
                  path="/"
                  component={() => {
                    return NotConnected(barrels);
                  }}
                />
              </Switch>

              {/**
               * Footer
               */}
              <div className="text-center">
                <span>
                  <LanguageSelector {...barrels} />
                </span>
              </div>
            </div>
          </div>
        </div>
      </Router>
    </>
  );
};
