/**
 * CSS
 ============================================================================ */
import "./Loading.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { Container, Row, Col } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
import Leads from "../leads";

export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Loading" };

  /**
        * Properties
        ============================================================================ */
  const props = Object.assign(defaults, args);
  //
  return (
    <>
      <Container fluid>
        <Row>
          <Col xl={4} lg={6} className="d-none d-lg-block" style={{}}>
            {/** Left column */}
          </Col>
          <Col lg={6} md={12}>
            <div className="lds-hourglass-container">
              <div className="lds-hourglass"></div>
            </div>
          </Col>
          <Col className="d-none d-xl-block" style={{}}>
            {/** Right column */}
          </Col>
        </Row>
      </Container>
    </>
  );
};
