import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Custom hooks
   */
  const { t } = useTranslation("Logout");

  /**
   * Barrels
   */
  const { error, info, language } = props.barrelGetters;
  const { setLanguage, setInfo, setError } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  const { onLogOut } = props.barrelEventHandlers;

  useEffect(() => {
    /**
     * Save vars
     */
    const tempError = error;
    const tempInfo = info;
    const tempLanguage = language;
    // const tempSessionTimeoutApplied = sessionTimeoutApplied;

    /**
     * Log out
     */
    onLogOut();

    /**
     * Restore vars
     */
    setLanguage(tempLanguage);
    // setSessionTimeoutApplied(tempSessionTimeoutApplied);
    // onSessionStart();
    // onSessionReset();

    // console.log("tempInfo: ", tempInfo);

    // if (tempError !== null) {
    //   showMessage(tempError, "error");
    // } else {
    //   if (tempInfo !== null) {
    //     showMessage(tempInfo, "info");
    //   }
    // }

    if (tempError !== null) {
      // showMessage(tempError, "error");
      setError(JSON.stringify(tempError));
    } else {
      setError(null);
    }

    if (tempInfo !== null) {
      // showMessage(tempInfo, "info");
      setInfo(JSON.stringify(tempInfo));
    } else {
      setInfo(null);
    }

    // eslint-disable-next-line
  }, []); // JSON.stringify(error), JSON.stringify(info) - logOut

  return (
    <>
      <h4>{t("See you soon")}</h4>
      <hr />

      <br />
      <p className="text-center">
        <a href="/">{t("Sign in")}</a>
      </p>
    </>
  );
};
