/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  // Redirect,
} from "react-router-dom";

/**
  * User imports
  ============================================================================ */
import Summary from "../summary";
import Alert from "../../alert";
import Login from "../../login";
import Logout from "../../logout";
import SignUp from "../../signup";
import PasswordReset from "../../passwordReset";
import LanguageSelector from "../../language-selector";
import ValidateEmail from "../../validateEmail";
import ChangePassword from "../../changePassword";
import Auth from "../../auth";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Main" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { signedIn } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getRedirect, getContainerClass } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const { wrapperClass, innerClass } = getContainerClass(signedIn);

  const appClass = signedIn ? "App" : "App bg";
  // const appClass = "App";

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    return (
      <Router>
        {getRedirect()}

        <div className={appClass} style={{}}>
          {/**
           * The alert
           */}
          <Alert data={args} />

          {/**
           * Wrapper for the main window
           */}
          <div className={wrapperClass}>
            {/**
             * Top margin of the main window (only for >= sm)
             */}
            <div className="d-none d-md-block" style={{ height: "1rem" }}></div>

            {/**
             * The main window
             */}
            <div className={innerClass}>
              {/**
               * Top padding of the main window
               */}
              <div style={{ height: "16px" }}></div>

              {/**
               * Routes
               */}
              <Switch>
                {/**
                 * Route: Home
                 */}

                <Route
                  exact
                  path="/"
                  component={() => {
                    return signedIn ? Summary(args) : Login(args);
                  }}
                />

                {/**
                 * Route: Sign in
                 */}
                <Route
                  exact
                  path="/sign-in"
                  component={() => {
                    return !signedIn ? Login(args) : Summary(args);
                  }}
                />

                {/**
                 * Route: Sign up
                 */}
                <Route
                  exact
                  path="/sign-up"
                  component={() => {
                    return SignUp(args);
                  }}
                />

                {/**
                 * Route: Sign up
                 */}
                <Route
                  exact
                  path="/auth"
                  component={() => (signedIn ? Auth(args) : Login(args))}
                />

                {/**
                 * Route: Validate mail
                 */}
                <Route
                  exact
                  path="/validate-email"
                  component={() => {
                    return ValidateEmail(args);
                  }}
                />

                {/**
                 * Route: Change password
                 */}
                <Route
                  exact
                  path="/password"
                  component={() => {
                    return ChangePassword(args);
                  }}
                />

                {/**
                 * Route: User profile
                 */}
                <Route
                  exact
                  path="/profile"
                  component={() => {
                    return signedIn ? Summary(args) : Login(args);
                  }}
                />

                {/**
                 * Route: Currency
                 */}
                <Route
                  exact
                  path="/currency"
                  component={() => {
                    return signedIn ? Summary(args) : Login(args);
                  }}
                />

                {/**
                 * Route: Sign out
                 */}
                <Route
                  exact
                  path="/sign-out"
                  component={() => {
                    return Logout(args);
                  }}
                />

                {/**
                 * Route: Password reset
                 */}
                <Route
                  exact
                  path="/reset"
                  component={() => {
                    return PasswordReset(args);
                  }}
                />

                {/**
                 * Route: Account registration
                 */}
                <Route
                  exact
                  path="/account-registration"
                  component={() => {
                    return Summary(args);
                  }}
                />

                {/**
                 * Route: Business registration
                 */}
                <Route
                  exact
                  path="/business-registration"
                  component={() => {
                    return Summary(args);
                  }}
                />

                {/**
                 * Route: My business
                 */}
                <Route
                  exact
                  path="/my-business"
                  component={() => {
                    return Summary(args);
                  }}
                />

                {/**
                 * Route: Balance
                 */}
                <Route
                  exact
                  path="/balance"
                  component={() => {
                    return Summary(args);
                  }}
                />

                {/**
                 * Route: Transfer
                 */}
                <Route
                  exact
                  path="/transfer"
                  component={() => {
                    return Summary(args);
                  }}
                />

                {/**
                 * Route: Contact
                 */}
                <Route
                  exact
                  path="/contact"
                  component={() => {
                    return Summary(args);
                  }}
                />

                {/**
                 * Route: Summary
                 */}
                <Route
                  exact
                  path="/summary"
                  component={() => {
                    return Summary(args);
                  }}
                />
              </Switch>

              {/**
               * Footer
               */}
              <div className="text-center">
                <span style={{ width: "40%" }}>
                  <LanguageSelector {...args} />
                </span>

                <span
                  className="text-muted"
                  style={{
                    float: "left",
                    marginLeft: "18px",
                    width: "30%",
                    textAlign: "left",
                  }}
                >
                  {signedIn &&
                    window.location.pathname !== "/contact" &&
                    window.location.pathname !== "/account-registration" && (
                      <small>&nbsp;</small>
                    )}
                </span>

                <span
                  className="text-muted"
                  style={{
                    float: "right",
                    marginRight: "18px",
                    width: "30%",
                    textAlign: "right",
                    // backgroundColor: "red",
                  }}
                >
                  {signedIn &&
                    window.location.pathname !== "/contact" &&
                    window.location.pathname !== "/account-registration" && (
                      <small>
                        <a href="/contact">{t("Contact")}</a>
                      </small>
                    )}
                </span>
              </div>
            </div>
          </div>
        </div>
      </Router>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
